import DetailHeader from './detailComponent/header'
import MissionDetail from './detailComponent/missionDetail'
import axios from 'axios'

export default {
  name: 'detail',
  components: {
    DetailHeader,
    MissionDetail
  },
  data () {
    return {
      mission: Object,
      name: ''
    }
  },
  methods: {
    getMission () {
      sessionStorage.setItem('mission_id', this.$route.params.miId)
      axios.get(this.url + '/mission/id/' + this.$route.params.miId)
        .then(this.getMissionSucc)
    },
    getMissionSucc (res) {
      if (res.status !== 200) alert(res)
      else if (res) {
        res = res.data
        this.mission = res
        this.name = res.name
      }
    }
  },
  mounted () {
    this.getMission()
  }
}



